import '../App.css'
import "../style.css"

import { useState, useEffect } from 'react'
import {BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';


export default function UI(props) {
  const [data, setData] = useState(props.data) 
  const [index, setIndex] = useState (0)
  const [num1, setNum1] = useState (0)

  const AdjustNum = (num) => {
     num++;
     return (num < 10) ? "0" + num : num
  }

  const soryByAtrr = (arr) => {
    return arr = arr.sort((a, b) => {
        let res = a["site_name"].localeCompare(b["site_name"])
            return res;
    })
  }




  const setTable = (e) => {
    e.preventDefault();
    setIndex(e.target.getAttribute('a-key'));
  }


  const getLinksLength = (links) => {
    return links.reduce ((a, c) => {
        return a + c.links.length;
    }, 0)
  }

  if (data == null || data == "") return <p>Null</p>

  return (
    <div className="container text-center" dir='rtl'>
      <div className="list-group text-right">
      <a href="javascript:void(0);" className="list-group-item active">
        <span className="badge text-left" style={{ float: 'left'}}>{ data.reduce((acc,element) => acc + getLinksLength(element.links), 0) }</span>
        קטגוריות אתרים
      </a>

      {
        data.map ((e, i) => {
          return (<a className="list-group-item" href="javascript:void(0);" key={i} a-key={i} onClick={ event => setTable(event) }>
            <span className="badge" style={{ float: 'left'}} a-key={i} onClick={ event => setTable(event) }>{ getLinksLength(e.links) }</span>
            {AdjustNum (i+1) + ". " + e.name}
          </a>) 
        })
      }
      </div>

      <select className="form-control" id="sel" onChange={ e => setIndex(e.target.value) } >
        {
          data.map ((e, i) => {
            return <option key={i+1} value={i}>{AdjustNum(i+1) + ". " + e.name + " - " + e.links.length + " קישורים" }</option> 
          })
        }
      </select>


      <div className="panel panel-info" style={{ marginTop: "1%" }}>
        <div className="panel-heading"><h3 style = {{ fontWeight: 'bolder'}} style={{ margin: '0', padding: '0.25em 0 0.25em 0'}}>{ data[index].name } </h3></div>
        <table className="panel-body table table-bordered table-striped table-hover table-condensed text-center" id="testTable">
          <thead>
            <tr style={{backgroundColor: "yellow" }}>
              <th className="numberCell">#</th>
              <th className="nameCell">שם</th>
              <th>קישור 2</th>
              <th>דף פייסבוק</th>
              <th>2דף פייסבוק</th>
              <th>לינקדאין</th>
              <th>אינסטגרם</th>
            </tr>
          </thead>
          <tbody id="dataTable">
          {
              data[index].links.map ((e1, i) => {
              return soryByAtrr(e1.links).map ((e2, j) => {
                return ([
                (j== 0) ? 
                <tr className="borderCat"><td colSpan="100"><h4>{ e1.cat }</h4></td></tr> : "",
                
                <tr key={j} className={ (j == 0) ? "borderTop": ''}> 
                  <td className="numberCell">{ AdjustNum(j)}</td>
                  <td className="nameCell">
                  { (e2.link != '') ? <a href={e2.link} >{e2.site_name}</a> : e2.site_name }
                  </td>
                  <td>
                  { (e2.link2 != '') ? <a href={e2.link2}>קישור 2</a> : 'קישור 2' }
                  </td>
                  <td>
                  { (e2.facebook_link1 != '') ? <a href={e2.facebook_link1}>דף פייסבוק</a> : 'דף פייסבוק' }
                  </td>
                  <td>
                  { (e2.facebook_link2 != '' && e2.facebook_link2 != undefined) ? <a href={e2.facebook_link2}>דף פייסבוק 2</a> : 'דף פייסבוק 2' }
                  </td>
                  <td>
                  { (e2.linkedIn_link != '') ? <a href={e2.linkedIn_link}>לינקדאין</a> : 'לינקדאין' }
                  </td>
                  <td>
                  { (e2.instagram_link != '' && e2.instagram_link != undefined) ? <a href={e2.instagram_link}>אינסטגרם</a> : 'אינסטגרם' }
                  </td>
                </tr>
              ])
            })
            }) 
          }
          </tbody>
        </table>
      </div>

    </div>
  )
}
  